<template>
  <form class="form" @submit.prevent="submitForm">
    <base-title>Registrierung</base-title>
    <base-textbox
      label="Emailadresse"
      id="email"
      :error="errors.email"
      v-model="email"
      :disabled="true"
      @remove-error="removeError('email')"
    ></base-textbox>
    <base-textbox
      label="Name"
      id="name"
      :error="errors.name"
      v-model="name"
      @remove-error="removeError('name')"
    ></base-textbox>
    <base-textbox
      label="Benutzername"
      id="username"
      :error="errors.username"
      error-message="Der Benutzername enthält ungültgige Zeichen."
      v-model="username"
      @remove-error="removeError('username')"
    ></base-textbox>
    <base-textbox
      label="Passwort"
      id="password"
      type="password"
      :error="errors.password"
      error-message="Mindestens 1 Großbuchstabe, 1 Kleinbuchstabe, 1 Zahl, und 1 Sonderzeichen erforderlich."
      v-model="password"
      @remove-error="removeError('password')"
    ></base-textbox>
    <base-textbox
      label="Passwort bestätigen"
      id="confirmPassword"
      type="password"
      :error="errors.confirmPassword"
      error-message="Die Passwörter stimmen nicht überein."
      v-model="confirmPassword"
      @remove-error="removeError('confirmPassword')"
    ></base-textbox>
    <base-textbox
      label="Sicherheitscode"
      id="token"
      :error="errors.token"
      v-model="token"
      @remove-error="removeError('token')"
    ></base-textbox>
    <base-button :loading="buttonLoading">Registrieren</base-button>
  </form>
  <base-modal title="Fehler" :open="modal.open" @close-modal="closeModal">{{
    modal.message
  }}</base-modal>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      username: "",
      password: "",
      confirmPassword: "",
      token: "",
      errors: {
        name: false,
        email: false,
        username: false,
        password: false,
        confirmPassword: false,
      },
      modal: {
        open: false,
        message: "",
      },
      buttonLoading: false,
    };
  },
  methods: {
    closeModal() {
      this.modal.open = false;
    },
    removeError(field) {
      this.errors[field] = false;
    },
    validateForm() {
      if (!this.name) {
        this.errors.name = true;
      }
      const usernameRegex = /^[a-zA-Z0-9._-]+$/;
      if (!this.username || !usernameRegex.test(this.username)) {
        this.errors.username = true;
      }
      const passwordRegex =
        /(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[._\-!?@#$%^&+=])./;
      if (!this.password || !passwordRegex.test(this.password)) {
        this.errors.password = true;
      } else if (this.confirmPassword !== this.password) {
        this.errors.confirmPassword = true;
      }
      if (!this.token) {
        this.errors.token = true;
      }
    },
    submitForm() {
      this.validateForm();
      if (
        this.errors.name ||
        this.errors.email ||
        this.errors.username ||
        this.errors.password ||
        this.errors.confirmPassword ||
        this.errors.token
      ) {
        return;
      }
      const data = {
        email: this.email,
        name: this.name,
        username: this.username,
        password: this.password,
        confirmPassword: this.confirmPassword,
        token: this.token,
      };
      const url = `${process.env.VUE_APP_API}/account/registration/${this.$route.params.userId}`;
      const options = {
        method: "POST",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      };
      this.buttonLoading = true;
      fetch(url, options)
        .then((res) => res.json())
        .then((res) => {
          if (res.ok) {
            this.$router.replace({ name: "login" });
          } else {
            this.modal.message =
              res.message ||
              "Ein Fehler ist aufgetreten, bitte versuche es später nochmal.";
            this.modal.open = true;
          }
        })
        .catch((error) => {
          console.log(error);
        })
        .finally(() => {
          this.buttonLoading = false;
        });
    },
  },
  mounted() {
    const fetchUrl = `${process.env.VUE_APP_API}/account/registration/${this.$route.params.userId}`;
    fetch(fetchUrl)
      .then((res) => res.json())
      .then((res) => {
        if (res.ok) {
          this.id = res.user._id;
          this.name = res.user.name;
          this.email = res.user.email;
        } else {
          this.$router.replace({ name: "login" });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>

<style lang="scss" scoped>
@use "../styles/index.scss" as *;
.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  &__link {
    align-self: center;
    margin-top: 1.5rem;
  }
}
</style>
